.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

div {
  box-sizing: border-box;
}
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 10%;
  padding-right: 10%;
  height: 50px;
  background-color: #000;
  color: #fff;
  font-size: 17px;
}

.AddListDiv {
  margin-left: auto;
}

.AddButton {
  margin-left: 10px;
  font-size: 14px;
}

.sortButton {
  margin-left: 30px;
  font-size: 14px;
}

.modal {
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}
.list {
  width: 80%;
  position: relative;
  border-bottom: 1px solid grey;
  padding: 30px;
  font-size: 1.2em;
  margin: auto;
}
.list span :hover {
}
.itemDiv {
}
.buttonDiv {
  position: absolute;
  top: 50%;
  right: 0;
}
.buttonDiv div {
  display: inline;
}
.largeButton {
  height: 50px;
  font-size: 18px;
  padding: 5px 10px;
  margin: 10px;
  border: 1px solid grey;
  border-radius: 10px;
}
.largeButton:hover {
  background-color: #00000025;
}
.emote {
  font-size: 1em;
}
